$dcBlue: #00388d;
$dcGold: #ffd54f;

.hero, .history {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  img {
    width: 1280px;
    height: 100%;
  }
}
.productInfo, .carousel {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 0 auto;
}

