.hero {
  background-image: url('../../images/hero-fpo.jpg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 500px;

  .hero-text {
    text-align: center;
    padding-left: 58%;
    padding-top: 2%;

    img {
      width: 150px;
      height: 70px;
    }
    h1 {
      width: 95%;
    }
  }
}
.history {
  background-image: url('../../images/dc-intro.jpg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: top left;
  height: 750px;

  .history-text {
    text-align: center;
    padding-left: 30%;
    padding-top: 4%;
    width: 75%;

    .history-upperDivider {
      background-image: url('../../images/3-drops.svg');
      background-repeat: repeat-x;
      width: 408px;
      height: 30px;
      margin: 0 auto 30px auto;
    } // history-upperDivider
    .history-lowerDivider {
      background-image: url('../../images/3-drops.svg');
      background-repeat: repeat-x;
      width: 135px;
      height: 30px;
      margin: 30px auto;
    } // history-lowerDivider
  } // history-text
} // history
.info {
  background-color: rgba(255, 255, 255, .8);
  width: 85%;
  margin: -170px auto 40px auto;
  box-shadow: -1px -1px 15px 3px rgba(196,196,196,0.6);

  .info-border {
    margin: 0 auto;
    // width: 98%;
    height: 20px;
    padding: 10px;
  } // info-border
  .info-whatIs {
    text-align: center;
    width: 82%;
    margin: 20px auto 40px auto;
  }
  .stepsIcon {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    border-bottom: 1px solid $gray400;
    width: 82%;
    padding-bottom: 40px;
    margin: 0 auto 40px auto;
    text-align: center;

    .step1, .step2 {
      width: 40%;

      h6 {
        border-bottom: 1px solid $gray400;
        width: 70px;
        margin: 20px auto;
      }
      p {
        margin: 0;
      }
    }
    .info-icon {
      width: 28%;
      margin: 0 40px;
    }
  } //stepsIcon
  .benefits {
    display: flex;
    flex-direction: row;
    align-content: flex-start;
    justify-content: center;
    width: 85%;
    margin: 0 auto;
  }
  .benefit {
    width: 25%;
    margin: 10px;
    display: flex;
    align-items: flex-start;

    svg {
      display: inline-block;
      margin-right: 8px;
      path {
        fill: $dcGold;
      } // path
      .blue {
        fill: $dcBlue;
      }
    }
    p {
      width: 75%;
      display: inline-block;
      font-size: 16px;

      .benefit-title {
        text-transform: uppercase;
        font-weight: 400;
        font-size: 15px;
      }
    }
  } // benefit
  .video {
    width: 90%;
    margin: 20px auto 0 auto;
    // @include clearfix;

    .video-thumbnail {
      max-width: 1068px;
      max-height: 150px;
      margin: 0 auto;
      position: relative;

      .video-text {
        position: absolute;
        top: 30px;
        left: 60px;

        img {
          display: inline-block;
          margin-right: 22px;
          width: 40px;
          vertical-align: middle;
        }
        h4 {
          display: inline-block;
          vertical-align: middle;
          color: #ffffff;
        }
      } // video-text
      .video-button {
        margin-right: 20px;
        width: 34px;
        height: 34px;
        vertical-align: middle;
      } // video-button
    } // video-thumbnail
  } // video
} // info
.productInfo {
  width: 100%;
  padding-bottom: 240px;

  h2 {
    margin: 4rem  auto;
  }
  #productsSlider {
    width: 90%;
    margin: 0 auto;
    padding-left: 3%;

    img {
      width: 320px;
      display: inline-block;
    }
    .productInfo-details {
      display: flex;
      align-items: center;
    }
    .productInfo-text {
      display: inline-block;
      width: 50%;
      margin-left: 32px;

      .productInfo-desc {
        margin: 20px 0;
      }
      .productDesc-cta {
        margin: 0;

        span {
          font-weight: 400;
          text-transform: uppercase;
        }
      } // productDesc-cta
    } // productInfo-text
    .slick-dots {
      bottom: -200px;

      li {
        height: 120px;
        width: 150px;
        margin: 10px;

        .img-wrapper {
          padding-right: 18px;
          border-right: 1px solid #222;
        } // img
        &:last-child img {
          border-right: none;
        }
      } // li
      .slick-active {
        // padding-bottom: 12px;
        height: 134px;
        a {
          width: 100px;
          border-bottom: 2px solid $dcBlue;
          padding-bottom: 6px;
        }
      } // slick-active
    } // slick-dots
  } // #productsSlider
} // productInfo
