.blog {
  height: 600px;
  margin: 0;
  padding: 0 0 20px 0;

  .blog-text {
    position: absolute;
    margin-left: 5%;
    display: inline-block;
    width: 33%;

    h2 {
      margin-bottom: 1rem;
    }
    h5 {
      font-size: 34px;
      line-height: 1.3;
      margin-top: 0;
      margin-bottom: 2.2rem;
      width: 60%;
    }
    p {
      margin-bottom: 2rem;
    }
  } // blog-text
  .toners-img {
    width: 100%;
    display: inline-block;
    float: right;

    img {
      width: 1280px;
      float: right;
    }
  } // toners-img
} // blog
